*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-size: 22px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  background-color: #282c34;
  margin: auto;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  height: 600px;
  color: white;
  max-width: 500px;
  border: 1px solid goldenrod;
  width: 500px;
  margin: 20px auto;
  padding: 20px;
  border-radius: 30px;
}

.Form {
  background-color: grey;
  width: 50%;
  height: 50%;
  display: flex;
  margin: auto;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
}

.offscreen {
  display: none;
}
.errMsg {
  display: inline;
}
.hide {
  display: none;
}
.valid {
  display: inline;
}
.instructions {
  background-color: black;
  color: aliceblue;
  font-size: 10px;
  font-family: verdana;
}
